<template>
  <div class="libraries">
    <LibraryCard v-for="(library, index) in libraries" :key="index" :library="library"/>
    <LibraryCard  @addedLibrary="updateLibraries"/>
  </div>
</template>

<script>
// @ is an alias to /src
import api from '@/services/Api'
import LibraryCard from '@/components/LibraryCard.vue'

export default {
  name: 'Libraries',
  components: {
    LibraryCard
  },
  data () {
    return {
      libraries: []
    }
  },
  methods: {
    updateLibraries (libraryData) {
      this.libraries.push(libraryData)
    }
  },
  created () {
    api.getLibraryList({ range: [20, 0] })
      .then(res => {
        this.libraries = res.data
      })
  }
}
</script>
